import Vue from 'vue'
import Vuex from 'vuex'
import actions from '@/vuex/actions'
import getters from '@/vuex/getters'
import mutations from '@/vuex/mutations'
import pack from '@/components/modules/sys/package/vuex/store' // store
import shopInfo from '@/components/modules/shop/info/vuex/store' // store

Vue.use(Vuex)

const state = {
  currentPathName: '', // 
  currentPathNameParent: '', // 
  serviceType: [], //  
  businessType: [], // 
  globalConfig: {},  // 
  listPerms: {} // 
}


//  store 
export default new Vuex.Store({
  state,
  getters,
  mutations,
  actions,
  modules: {
    pack,
    shopInfo
  }
})


