const state = {
  longitude: '', // 
  latitude: '', // 
  address: '', // 
  tabDisabled: true, //   tab 
  tabActive: 'basicInfo', // tab
  shopInfoRecId: '', // id
  contacts: [], // 
  site: [] // 
}

const getters = {
  getLongitude: state => state.longitude,
  getLatitude: state => state.latitude,
  getTabDisabled: state => state.tabDisabled,
  getTabActive: state => state.tabActive,
  getShopInfoRecId: state => state.shopInfoRecId,
  getContacts: state => state.getContacts,
  getSite: state => state.site,
  getAddress: state => state.address
}

const mutations = {

  // 
  updateAddress(state, str) {
    state.address = str
  },

  //  Contacts
  updateContacts(state, arr) {
    state.contacts = arr
  },
  // 
  updateSite(state, arr) {
    state.site = arr
  },
  // 
  updateLongitude(state, longitude) {
    state.longitude = longitude
  },

  // 
  updateLatitude(state, latitude) {
    state.latitude = latitude
  },

  // tab
  updateTabDisabled(state, bol) {
    state.tabDisabled = bol
  },

  // tab
  updateCurrentTab(state, tab) {
    state.tabActive = tab
  },

  // id
  updateShopInfoRecId(state, id) {
    state.shopInfoRecId = id
  }

}

export default {
  state,
  getters,
  mutations
}


